@import '../../node_modules/rmc-tabs/assets/index.css';
@import '../../node_modules/rmc-date-picker/assets/index.css';
@import '../../node_modules/rmc-picker/assets/index.css';
@import '../../node_modules/rmc-picker/assets/popup.css';
.text-ellipse {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.daily-wrapper {
  font-size: 14px;
  background-color: #f5f5f9;
}
.daily-wrapper * {
  padding: 0;
  margin: 0;
}
.daily-wrapper > header {
  display: flex;
  box-sizing: border-box;
  height: 1.8rem;
  background-color: #fff;
}
.daily-wrapper > header > section {
  box-sizing: border-box;
  width: 33.3333333%;
  padding: 0.4rem 0;
}
.daily-wrapper > header > section > span {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding: 0 0.4rem;
}
.daily-wrapper > header > section > span span {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.daily-wrapper > header > section:first-of-type > ul {
  position: absolute;
  z-index: 100;
  top: 1.8rem;
  left: 0;
  right: 0;
  list-style-type: none;
  padding: 0;
  margin: 0;
  background-color: #fff;
}
.daily-wrapper > header > section:first-of-type > ul > li {
  height: 1.6rem;
  line-height: 1.6rem;
  padding: 0 0.8rem;
  border-bottom: 0.04rem solid #eee;
}
.daily-wrapper > header > section:first-of-type > ul > li.active {
  color: #BDA571;
}
.daily-wrapper > header > section:not(:first-of-type) > span {
  border-left: 0.04rem solid #eee;
}
.daily-wrapper > main {
  background-color: #fff;
  margin-top: 0.4rem;
}
.daily-wrapper > main .info-item {
  border-bottom: 0.04rem solid #e4e4e4;
  padding: 0.4rem 0;
  min-height: 3.48rem;
}
.daily-wrapper > main .info-item > .info > p {
  padding: 0 0.8rem;
}
.daily-wrapper > main .info-item > .detail {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.45);
  border-top: 0.04rem solid #e4e4e4;
  padding: 0.8rem 1.2rem 0.4rem;
  margin-top: 0.4rem;
}
.month {
  width: 15rem;
}
.month .active {
  background-color: #bda571 !important;
  color: #fff !important;
  border-color: #bda571 !important;
}
.month .rmc-tabs-tab-bar-top .rmc-tabs-tab-bar-tab,
.month .rmc-tabs-tab-bar-bottom .rmc-tabs-tab-bar-tab {
  padding: 0.48rem 0;
  font-size: 0.64rem;
}
.month .but-box {
  text-align: center;
  padding: 0.48rem 0;
}
.month .tab-item {
  background: #f3f3f3;
  padding-top: 0.24rem;
}
.month .tab-item h5 {
  height: 1.2rem;
  margin-bottom: 0px;
  background: #fff;
  line-height: 1.4rem;
  font-size: 0.6rem;
}
.month .tab-item h5 span {
  margin-left: 0.6rem;
  border-left: 0.1rem solid #bda571;
  padding-left: 0.2rem;
}
.month .tab-item .echarts {
  height: 400px;
  width: 100vw;
  background: #fff;
  margin-bottom: 0.24rem;
}
.month .tab-item .shop-items {
  border-top: 0.1rem solid #f3f3f3;
  background: #fff;
  padding: 0px 0.64rem;
}
.month .tab-item .shop-items .item {
  width: 13.72rem;
  height: 2.68rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 0.04rem solid #f3f3f3;
}
.month .tab-item .shop-items .item .left {
  width: 15%;
  height: 100%;
  display: flex;
  align-items: center;
}
.month .tab-item .shop-items .item .right {
  width: 85%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 1% 0;
}
.month .tab-item .shop-items .item .right h6 {
  font-size: 0.6rem;
}
.month .tab-item .shop-items .item .right div {
  display: flex;
  justify-content: space-between;
}
.month .date-box {
  display: flex;
  align-items: center;
  border-bottom: 0.04rem solid #d9d9d9;
  padding: 0 0.4rem 0.4rem 0.4rem;
}
.month .date-box .title {
  width: 2.4rem;
  min-width: 60px;
  font-size: 0.48rem;
  flex-shrink: 0;
  white-space: nowrap;
}
.month .date-box .statr-time span,
.month .date-box .end-time span {
  height: 100%;
  display: flex;
}
.month .date-box .statr-time span .statr-time-box,
.month .date-box .end-time span .statr-time-box {
  height: 100%;
  border-radius: 0.08rem;
  display: flex;
  width: 100%;
  background: none;
  outline: none;
  align-items: center;
  justify-content: space-around;
  border: 0.04rem solid #d9d9d9;
}
